.main-nav-toggle {
  position: relative;
  z-index: 2;
  display: block;
  width: 64px;
  height: 64px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .main-nav-toggle {
    display: none;
  }
}
.main-nav-toggle::before, .main-nav-toggle::after {
  content: "";
  position: absolute;
  top: 47%;
  left: 81%;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.main-nav-toggle::before {
  transform: translate(-50%, -8px);
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.main-nav-toggle::after {
  display: none;
  transform: translate(-50%, 0) rotate(-45deg);
}
.main-nav-toggle--opened::before {
  transform: translate(-50%, 0) rotate(45deg);
  box-shadow: none;
}
.main-nav-toggle--opened::after {
  display: block;
}
.main-nav-toggle--opened + .main-nav {
  display: block;
}

.main-nav {
  display: none;
  position: absolute;
  left: 0;
  top: 100%;
  width: 100%;
  background-color: #ffffff;
  z-index: 100;
}
